import React, { useState, useEffect } from 'react';
import { FluentProvider, Input, Textarea, Dropdown, Option, OptionOnSelectData } from '@fluentui/react-components';
import Layout from "../components/layout";
import { planktonTheme } from "../planktonTheme";
import * as styles from './project-request.module.css';
import emailjs from 'emailjs-com';
import {useTranslation} from 'gatsby-plugin-react-i18next';
import { graphql } from 'gatsby';
import { useLocation } from '@reach/router'; // useLocation is from react-router-dom if you're not using Gatsby


import {
    NumberCircle132Filled,
    NumberCircle232Filled,
    NumberCircle332Filled,
    NumberCircle432Filled
} from '@fluentui/react-icons';


const initialFormState = {
    services: [],
    projectDescription: '',
    projectType: '',
    question1: '',
    question2: '',
    question3: '',
    question4: '',
    salutation: '',
    name: '',
    companyName: '',
    email: '',
    phone: '',
    additionalInfo: ''
};

const validateEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
};

const validatePhoneNumber = (phoneNumber) => {
    const phoneRegex = /^[0-9]{10,15}$/; // Adjust this regex based on your phone number format
    return phoneRegex.test(phoneNumber);
};

const ProjectRequest: React.FC = () => {
    const location = useLocation();
    const selected = location.state == undefined ? "" : location.state?.selected
    const [step, setStep] = useState(1);
    const [formData, setFormData] = useState(initialFormState);
    const [submitted, setSubmitted] = useState(false);
    const [errors, setErrors] = useState({
        email: '',
        phone: '',
    });
    const [contactDataSet, setContactDataSet] = useState(false);
    const { t } = useTranslation();

    useEffect(() => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth' // Optional: adds smooth scrolling
        });
    }, [step]);

    useEffect(() => {
        const emailError = validateEmail(formData.email) ? '' : t('Invalid email address');
        const phoneError = validatePhoneNumber(formData.phone) ? '' : t('Invalid phone number');
        setErrors({
            email: emailError,
            phone: phoneError,
        });
    }, [formData.email, formData.phone]);

    useEffect(()=> {
        switch(selected) {
            case "website": {
                handleCheckboxChange("Website")
                handleNextStep()
                break
            }
            case "logo": {
                handleCheckboxChange("Logo")
                handleNextStep()
                break
            }
            case "uiux": {
                handleCheckboxChange("UI/UX design")
                handleNextStep()
                break
            }
            case "webApp": {
                handleCheckboxChange("Web application")
                handleNextStep()
                break
            }
            case "mobileApp": {
                handleCheckboxChange("Mobile application")
                handleNextStep()
                break
            }
            case "apiIntegration": {
                handleCheckboxChange(`${t('Third party API integration in your system')}`)
                handleNextStep()
                break
            }
            case "ai":{
                handleCheckboxChange(`${t('AI solution')}`)
                handleNextStep()
                break;
            }
            case "withRequirments": {
                var selectedOption = options.find(option => option.key === 'newProject');
                handleDropdownChange({optionValue: selectedOption.text})
                break;
            }
            case "noRequirments": {
                var selectedOption = options.find(option => option.key === 'projectIdea');
                handleDropdownChange({optionValue: selectedOption.text})
                break;
            }
            default:
                console.log("case default: " + selected)
                break

        }

    }, [])

    useEffect(() => {
        const isValid = formData.salutation && formData.name && formData.email && formData.phone
        setContactDataSet(isValid);
    }, [formData, errors]);

    const options = [
        { key: 'newProject', text: t('New project with clear specifications') },
        { key: 'projectIdea', text: t('A project idea without specifications') },
        { key: 'modernizing', text: t('Modernizing existing solution') },
    ];

    const salutationOptions = [
        { key: 'mr', text: t('Mr.') },
        { key: 'mrs', text: t('Mrs./Ms') },
        { key: 'other', text: t('Other')  }
    ];

    const handleCheckboxChange = (service: string) => {
        setFormData(prevState => {
            const services = prevState.services.includes(service)
                ? prevState.services.filter(s => s !== service)
                : [...prevState.services, service];
            return { ...prevState, services };
        });
    };

    const handleRadioChange = (question: string, option: string) => {
        setFormData(prevState => ({ ...prevState, [question]: option }));
    };

    const handleInputChange = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
        const { name, value } = event.target;
        setFormData(prevState => ({ ...prevState, [name]: value }));
    };

    const handleDropdownChange = (data: OptionOnSelectData) => {
        console.log("test dropdown: " + data.optionValue);
        setFormData(prevState => ({ ...prevState, projectType: data.optionValue }));
    };

    const handleSalutationDropdownChange = (event: React.FormEvent<HTMLDivElement>, data: OptionOnSelectData) => {
        setFormData(prevState => ({ ...prevState, salutation: data.optionValue }));
    };

    const handleNextStep = () => setStep(prevStep => prevStep + 1);
    const handlePrevStep = () => setStep(prevStep => prevStep - 1);

    const stepIcons = [
        <NumberCircle132Filled key="step1" />,
        <NumberCircle232Filled key="step2" />,
        <NumberCircle332Filled key="step3" />,
        <NumberCircle432Filled key="step4" />
    ];

    const renderStepIndicator = () => (
        <div className={styles.stepIndicator}>
            {[
                t('Project Inquiry'),
                t('Your Projects'),
                t('Questions'),
                t('Contact Details'),
            ].map((stepName, index) => (
                <div key={index} className={styles.stepWrapper}>
                    <div className={`${styles.stepNumber} ${step === index + 1 ? styles.activeStep : ''}`}>
                        {stepIcons[index]}
                    </div>
                    {index < 3 && (
                        <div className={`${styles.stepLine} ${step > index + 1 ? styles.activeLine : ''}`} />
                    )}
                    <div className={styles.stepLabel}>{stepName}</div>
                </div>
            ))}
        </div>
    );




    const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        setSubmitted(true);

        const emailError = validateEmail(formData.email) ? '' : 'Invalid email address';
        const phoneError = validatePhoneNumber(formData.phone) ? '' : 'Invalid phone number';
        setErrors({
            email: emailError,
            phone: phoneError,
        });

        if (!emailError && !phoneError && contactDataSet) {
            // Proceed with form submission
            emailjs.send('service_061vzkc', 'template_46f7hsp', formData, 'cA4lsemzX-4esmxhu')
                .then((response) => {
                    console.log('SUCCESS!', response.status, response.text);
                }, (error) => {
                    console.log('FAILED...', error);
                });
            window.location.href = '/thank-you-for-request';
        } else {
            console.log('Form contains errors');
        }
    };

    const questions = [
        {
            question: t('Who are you making this request as?'),
            options: [
                t('An individual looking for personal use'),
                t('A small business owner (1-10 employees)'),
                t('A representative of a medium-sized company (11-50 employees)'),
                t('A representative of a large corporation (50+ employees)')
            ]
        },
        {
            question: t('What is the main goal you want to achieve with this project?'),
            options: [
                t('Increase online presence and reach more customers'),
                t('Improve internal processes and efficiency'),
                t('Launch a new product or service'),
                t('Integrate with existing systems to enhance functionality')
            ]
        },
        {
            question: t('What is your expected timeline for the project delivery?'),
            options: [
                t('Within 1-3 months'),
                t('Within 3-6 months'),
                t('Within 6-12 months'),
                t('More than 12 months')
            ]
        },
        {
            question: t('What is your budget range for this project?'),
            options: [
                t('Less than $10,000'),
                t('$10,000 - $50,000'),
                t('$50,000 - $100,000'),
                t('More than $100,000')
            ]
        }
    ];

    return (
        <Layout>
            <FluentProvider theme={planktonTheme}>
                <form onSubmit={handleSubmit} className={styles.formContainer}>
                    {renderStepIndicator()}

                    {step === 1 && (
                        <div className={styles.step}>
                            <h2>{t('How can we help you')}?</h2>
                            <div className={styles.checkboxes}>
                                {['UI/UX design', 'Website', 'Logo', 'Web application', 'Mobile application', `${t('Third party API integration in your system')}`, `${t('AI solution')}`, `${t('Something else')}`].map(service => (
                                    <label
                                        key={service}
                                        className={`${styles.checkboxContainer} ${formData.services.includes(service) ? styles.selected : ''}`}
                                        onClick={() => handleCheckboxChange(service)}
                                    >
                                        <input
                                            type="checkbox"
                                            name={service}
                                            checked={formData.services.includes(service)}
                                            onChange={() => handleCheckboxChange(service)}
                                            className={styles.checkbox}
                                        />
                                        <span className={styles.checkboxLabel}>{service}</span>
                                    </label>
                                ))}
                            </div>
                            <div>
                                <button disabled={formData.services.length === 0} className={`${styles.button} ${formData.services.length === 0 ? styles.disabledButton : ''}`} onClick={handleNextStep}>
                                    <span>{t('Continue to project')}</span>
                                </button>
                            </div>
                        </div>
                    )}

                    {step === 2 && (
                        <div className={styles.step}>
                            <h2>{t('Your projects')}</h2>
                            <div className="flex-column flex-12" style={{ paddingBottom: '10px' }}>
                                <label className="flex-12" htmlFor="projectType">{t('Select project type')}: <span className={styles.requiredField}>*</span></label>
                                <Dropdown
                                    value={formData.projectType}
                                    onOptionSelect={(event, data) => handleDropdownChange(data)}
                                    className="flex-12"
                                >
                                    {options.map(option => (
                                        <Option key={option.key} value={option.text}>
                                            {option.text}
                                        </Option>
                                    ))}
                                </Dropdown>
                            </div>
                            <div className="flex-column flex-12" style={{ paddingBottom: '10px' }}>
                                <label className="flex-12" htmlFor="projectDescription">{t('Describe your project')}: <span className={styles.requiredField}>*</span></label>
                                <Textarea
                                    value={formData.projectDescription}
                                    onChange={handleInputChange}
                                    id="projectDescription"
                                    name="projectDescription"
                                    className="flex-12"
                                    rows={7}
                                />
                            </div>

                            <div className={styles.buttonGroup}>
                                <button className={` ${styles.simpleButton}`} onClick={handlePrevStep}>
                                    <span>{t('Back')}</span>
                                </button>
                                <button disabled={formData.projectType.length === 0 || formData.projectDescription.length === 0} className={`${styles.button} ${formData.projectType.length === 0 || formData.projectDescription.length === 0 ? styles.disabledButton : ''}`} onClick={handleNextStep}>
                                    <span>{t('Continue to questions')}</span>
                                </button>
                            </div>
                        </div>
                    )}

                    {step === 3 && (
                        <div className={styles.step}>
                            <h2>{t('Project questions')}</h2>
                            {questions.map((question, index) => (
                                <div key={index} className={styles.radioGroup}>
                                    <p>{question.question}</p>
                                    <div className={styles.radioOptions}>
                                        {question.options.map(option => (
                                            <label
                                                key={option}
                                                className={`${styles.radioContainer} ${formData[`question${index + 1}`] === option ? styles.selected : ''}`}
                                                onClick={() => handleRadioChange(`question${index + 1}`, option)}
                                            >
                                                <input
                                                    type="radio"
                                                    name={`question${index + 1}`}
                                                    value={option}
                                                    checked={formData[`question${index + 1}`] === option}
                                                    onChange={() => handleRadioChange(`question${index + 1}`, option)}
                                                    className={styles.radio}
                                                />
                                                <span className={styles.radioLabel}>{option}</span>
                                            </label>
                                        ))}
                                    </div>
                                </div>
                            ))}
                            <div className={styles.buttonGroup}>
                                <button className={` ${styles.simpleButton}`} onClick={handlePrevStep}>
                                    <span>{t('Back')}</span>
                                </button>
                                <button className={`${styles.button}`} onClick={handleNextStep}>
                                    <span>{t('Continue to contact details')}</span>
                                </button>
                            </div>
                        </div>
                    )}

                    {step === 4 && (
                        <div className={styles.step}>
                            <h2>Contact Details</h2>
                            <div className="flex-column flex-12" style={{ paddingBottom: '10px' }}>
                                <label className="flex-12" htmlFor="salutation">{t('Salutation')}: <span className={styles.requiredField}>*</span></label>
                                <Dropdown
                                    value={formData.salutation}
                                    onOptionSelect={(event, data) => handleSalutationDropdownChange(event, data)}
                                    className="flex-12"
                                >
                                    {salutationOptions.map(option => (
                                        <Option key={option.key} value={option.key}>
                                            {option.text}
                                        </Option>
                                    ))}
                                </Dropdown>
                            </div>
                            <div className="flex-column flex-12" style={{ paddingBottom: '10px' }}>
                                <label className="flex-12" htmlFor="name">Name: <span className={styles.requiredField}>*</span></label>
                                <Input
                                    value={formData.name}
                                    onChange={handleInputChange}
                                    id="name"
                                    name="name"
                                    required
                                    className="flex-12"
                                />
                            </div>
                            <div className="flex-column flex-12" style={{ paddingBottom: '10px' }}>
                                <label className="flex-12" htmlFor="email">Email: <span className={styles.requiredField}>*  {submitted && errors.email && errors.email}</span></label>

                                <Input
                                    value={formData.email}
                                    onChange={handleInputChange}
                                    id="email"
                                    name="email"
                                    required
                                    className={`flex-12 ${submitted && errors.email ? styles.inputError : ''}`}

                                />

                            </div>
                            <div className="flex-column flex-12" style={{ paddingBottom: '10px' }}>
                                <label className="flex-12" htmlFor="phone">{t('Phone')}: <span className={styles.requiredField}>* {submitted && errors.phone && errors.phone}</span></label>
                                <Input
                                    value={formData.phone}
                                    onChange={handleInputChange}
                                    id="phone"
                                    name="phone"
                                    required
                                    className={`flex-12 ${submitted && errors.email ? styles.inputError : ''}`}
                                />

                            </div>
                            <div className="flex-column flex-12" style={{ paddingBottom: '10px' }}>
                                <label className="flex-12" htmlFor="companyName">{t('Company Name')}:</label>
                                <Input
                                    value={formData.companyName}
                                    onChange={handleInputChange}
                                    id="companyName"
                                    name="companyName"
                                    className="flex-12"
                                />
                            </div>
                            <div className="flex-column flex-12" style={{ paddingBottom: '10px' }}>
                                <label className="flex-12" htmlFor="additionalInfo">{t('Additional Information')}:</label>
                                <Textarea
                                    value={formData.additionalInfo}
                                    onChange={handleInputChange}
                                    id="additionalInfo"
                                    name="additionalInfo"
                                    className="flex-12"
                                    rows={5}
                                />
                            </div>
                            <div className={styles.buttonGroup}>
                                <button className={`${styles.simpleButton}`} onClick={handlePrevStep}>
                                    <span>{t('Back')}</span>
                                </button>
                                <button disabled={!contactDataSet} className={`${styles.button} ${!contactDataSet ? styles.disabledButton : ''}`} type="submit">
                                    <span>{t('Submit request')}</span>
                                </button>
                            </div>
                        </div>
                    )}
                </form>
            </FluentProvider>
        </Layout>
    );
};

export default ProjectRequest;
export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: {language: {eq: $language}}) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;